.team {
  background-color: $color;

  .title {
    @extend %text700;
    font-size: 17px;
    text-align: center;
    padding-top: 30px;
    padding-bottom: 30px;
    color: $texcolor;

    @media screen and (min-width: $tablet) {
      padding-top: 0px;
      padding-bottom: 70px;
      font-size: 1.5rem;
    }

    @media screen and (min-width: $desktop) {
      font-size: 2rem;
    }
  }

  .footer_ul {
    margin-bottom: 0px;
  }

  .team__ul {
    list-style: none;

    @media screen and(min-width: $tablet) {
      display: flex;
      flex-wrap: wrap;
      gap: 30px;
    }
  }

  .team__li {
    padding-left: 0;
    border-radius: 0px 0px 4px 4px;
    margin-bottom: 30px;

    @media screen and (min-width: $desktop) {
      margin-bottom: 0px;
    }

    .figcaption {
      width: 100%;
      margin-top: -0.3rem;
      padding: 30px 0px;
      background-color: $colorwhite;
      color: $texcolor;
      font-weight: 500;
      font-size: 16px;
      line-height: 1.17;
      text-align: center;
      border-radius: 0px 0px 4px 4px;

      &_p {
        padding-top: 10px;
        @extend %text400;
        color: $colorp;
      }
    }
  }

  figure {
    box-shadow: 10px 10px 5px 1px rgba(219, 219, 219, 1);
  }
}
